       url("../fonts/gt-haptik/GT-Haptik-Regular.otf") format("otf"),
       url("../fonts/gt-haptik/GT-Haptik-Regular.woff") format("woff"),
       url("../fonts/gt-haptik/GT-Haptik-Regular.woff2") format("woff2"),
       url("../fonts/gt-haptik/GT-Haptik-Regular.eot") format("eot");
}

