.infor {
  display: flex;
  position: relative;
  top: 80vh;
  padding: 4rem 0 44rem 0;
  z-index: 500;
  background-color: #BFE9FF; }

.experience {
  padding: 3rem 0;
  /* background: #f7f7f7; */
  text-align: left;
  display: flex; }

.section-title {
  position: relative;
  width: 70%;
  // p
 }  //   margin: 0px

.section-content {
  position: relative;
  with: 70%;
  text-align: left;
  margin: 0px;
  // p
 }  //   margin: 0px

.title {
  color: $black;
  font-size: 1.5rem;
  font-weight: 700; }


.category {
  font-size: 1rem;
  color: $gray; }


.img-content {
  display: none; }


.social, .copyright {
  margin: 1rem; }


.social li {
  display: inline-block; }


.text-container {
  width: 100%;
  height: 300px; }


@supports (display: grid) {
  .gallery .grid {
    grid-gap: 1rem; } }



@media(min-width: $md) {
  nav ul {
    -webkit-box-pack: right;
    -ms-flex-pack: right;
    justify-content: right; } }



@media screen and (max-width: 1024px) {
  .blurb {
    font-size: 2.2rem; }
  img:hover {
    -webkit-transform: none;
    -ms-transform: none;
    transform: none; }


  .img-container {
    height: 100%;
    // &:hover .img-content-hover
 }    //   display: none



  .img-content {
    display: block;
    padding: 1rem 0; } }

